import { Typography } from "@mui/material";
import { Stack } from "@mui/system";
import React from "react";
import {
  NowPlayingMoviesApi,
  PopularMoviesApi,
  TopRatedApi,
} from "../../../config/apis";
import Slider from "../Slider/Slider";

export default function DiscoverContent() {
  return (
    <Stack>
      {arrayMode.map((mode, index) => (
        <Stack key={index} spacing={3} mt={3} ml={4}>
          <Typography sx={{color:"primary.main", fontFamily:"NBOLD", fontSize:"1.2rem"}}>{mode.label}</Typography>
          <Slider mode={mode} />
        </Stack>
      ))}
    </Stack>
  );
}

const arrayMode = [
  {
    id: "Cinema",
    label: "Playing Now on Cinemas",
    api: NowPlayingMoviesApi,
  },
  {
    id: "TopRated",
    label: "Top Rated",
    api: TopRatedApi,
  },
  {
    id: "Popular",
    label: "Popular",
    api: PopularMoviesApi,
  },
];
