import { Box } from "@mui/system";
import React from "react";
import bg from "../../../assets/imgs/bg.jpg";
import { ImagesBaseUrl } from "../../../config/apis";
export const NowPlayingTrailer = ({ trailer, nowPlayingMovie }) => {
  return (
    <>
      <img
        style={{
          width: "100%",
          height: "100%",
          position: "absolute",
          zIndex: -1,
        }}
        src={
          nowPlayingMovie?.backdrop_path || nowPlayingMovie?.poster_path
            ? `${ImagesBaseUrl}${
                nowPlayingMovie?.backdrop_path || nowPlayingMovie?.poster_path
              }`
            : bg
        }
      />
      {trailer ? (
        <Box sx={{ width: "calc(100%)", height: "100%" }}>
          <iframe
            width="100%"
            height="100%"
            frameBorder="0"
            src={`https://www.youtube.com/embed/${trailer}?controls=0&autoplay=1&modestbranding=0&rel=0&mute=1&loop=1&playlist=${trailer}`}
          ></iframe>
        </Box>
      ) : (
        <Box sx={{ width: "calc(100% - 2px)" }}>
          <img
            src={
              nowPlayingMovie?.poster_path || nowPlayingMovie?.backdrop_path
                ? `${ImagesBaseUrl}${
                    nowPlayingMovie?.poster_path ||
                    nowPlayingMovie?.backdrop_path
                  }`
                : bg
            }
            style={{ width: "100%", height: "100%", objectFit: "cover" }}
          />
        </Box>
      )}
    </>
  );
};
