import { useCallback, useEffect, useState } from "react";
import {
  LatestMoviesApi,
  NowPlayingMoviesApi,
  PopularMoviesApi,
} from "../../config/apis";
import { GetMoviesList } from "../apisConnections/getMoviesList";
import { multiSearch } from "../apisConnections/multiSearch";

export default function useMoviesHook() {
  const [movies, setMovies] = useState([]);
  const [nowPlayingMovie, setNowPlayingMovie] = useState({});
  const [nowPlayingMovies, setNowPlayingMovies] = useState([]);
  const fetchPopularMovies = useCallback(async (pageNumber) => {
    const fetchedMovies = await GetMoviesList(pageNumber, PopularMoviesApi);
    if (fetchedMovies) setMovies([]);
  }, []);

  const fetchMoviesByApi = useCallback(async (api, pageNumber) => {
    const fetchedMovies = await GetMoviesList(pageNumber, api);
    if (fetchedMovies?.results) setMovies([...fetchedMovies?.results]);
    console.log(fetchedMovies);
  }, []);

  const fetchLatestMovie = useCallback(async (pageNumber) => {
    const fetchedMovies = await GetMoviesList(pageNumber, LatestMoviesApi);
    if (fetchedMovies) setMovies([]);
  }, []);

  const fetchNowPlayingMovie = useCallback(async (pageNumber) => {
    const fetchedMovies = await GetMoviesList(pageNumber, NowPlayingMoviesApi);
    if (
      fetchedMovies &&
      fetchedMovies?.results &&
      fetchedMovies?.results?.length > 0
    )
      setNowPlayingMovie({ ...fetchedMovies.results[0] });
  }, []);

  const fetchNowPlayingMovies = useCallback(async (pageNumber) => {
    const fetchedMovies = await GetMoviesList(pageNumber, NowPlayingMoviesApi);
    if (fetchedMovies?.results) setNowPlayingMovies([...fetchedMovies.results]);
  }, []);

  const SearchAll = useCallback(
    async (query, pageNumber) => {
      const fetchedMovies = await multiSearch(query, pageNumber);
      if (fetchedMovies) {
        setMovies([...fetchedMovies]);
      }
    },
    [setMovies]
  );

  return {
    movies,
    nowPlayingMovie,
    nowPlayingMovies,
    fetchPopularMovies,
    fetchLatestMovie,
    fetchNowPlayingMovie,
    fetchNowPlayingMovies,
    SearchAll,
    fetchMoviesByApi,
  };
}
